@font-face {
  font-family: "QuicksandLocal";
  src: url("/Survey/fonts/informal/quicksand-v31-latin-regular.eot");
  src: url("/Survey/fonts/informal/quicksand-v31-latin-regular.woff"), url("/Survey/fonts/informal/quicksand-v31-latin-regular.ttf") format("truetype");
