input[type=button],
input[type=submit],
input[type=reset],
button[class^=a-button] {
  display: block;
  box-sizing: content-box;
  min-width: 3em;
  min-height: 30px;
  -webkit-appearance: none;
  margin: 0 0 12px 0;
  border: 1px solid #E8BE5D;
  border-radius: 5px;
  padding: 4px 1em;
  background-color: #E8BE5D;
  color: #FAFAFA;
  white-space: normal;
  font-size: 20px;
  line-height: 24px;
}

input[type=button]:hover,
input[type=submit]:hover,
input[type=reset]:hover,
button:hover {
  background-color: #D4AA49 !important;
  color: #FAFAFA;
}

input[type=button]:active,
input[type=submit]:active,
input[type=reset]:active {
  background-color: #FAFAFA !important;
  color: #E8BE5D;
}

button:disabled,
input[type=button]:disabled,
input[type=submit]:disabled,
input[type=reset]:disabled {
  border-color: #847E88 !important;
  background-color: #847E88 !important;
  color: #FAFAFA !important;
}

button[data-checked=true],
input[type=button][data-checked=true],
input[type=submit][data-checked=true] {
