}

.m-list-optionlist input:focus + label,
.m-list-optionlist input:focus-visible + label {
  outline: none;
}
.m-list-optionlist .a-icon-multistate {
  left: -2px;
}

html[dir=rtl] .m-list-optionlist .a-icon-multistate {
  left: auto;
  right: -2px;
}

textarea {
  width: 100%;
  background: #FFFFFF;
  border: 1px solid #4A444E;
  box-sizing: border-box;
  border-radius: 3px;
  padding: 8px;
  min-height: 100px;
  color: #7A3996;
  overflow-y: auto;
  font-size: 18px;
  font-weight: 400;
  line-height: 24px;
}

textarea.has-content {
  border-color: transparent;
  background-color: rgba(232, 190, 93, 0.2);
}

textarea:active,
textarea:focus,
textarea:hover {
  outline: none;
}

textarea:focus {
  border-color: #E8BE5D;
}

textarea:focus-visible,
textarea:focus-within {
  outline: none;
  border-color: #E8BE5D;
}

/* a-input-singlelineedit
 * essential styling for the fundamental HTML input element
 */
.a-input-singlelineedit {
  padding: 8px;
  border-radius: 3px;
  border: 1px solid #4A444E;
  color: #7A3996;
  margin: 0 0 0.5em 0;
  width: 197px;
  background-color: #FFFFFF;
  box-sizing: content-box;
  font-size: 18px;
  line-height: 24px;
}

.a-input-singlelineedit.has-content {
  border-color: transparent;
  background-color: rgba(232, 190, 93, 0.2);
}

.balance .a-input-singlelineedit,
.balance .m-input-singlelineedit {
  margin-right: 1em;
}

html[dir=rtl] .balance .a-input-singlelineedit,
html[dir=rtl] .balance .m-input-singlelineedit {
  margin-right: auto;
  margin-left: 1em;
}

/* m-input-singlelineedit
 * the molecule gets added by JavaScript when the a-input-singlelineedit
 * includes a pre or post label and therefore requires an additional wrapper
 * overflow: hidden added to fix issue with display in IE10
 */
.m-input-singlelineedit {
  display: inline-block;
  /* prevent double drop shadow when input is within a molecule container */
  margin-bottom: 0.5em;
}
.m-input-singlelineedit.focused .a-input-singlelineedit {
  box-shadow: none !important;
}
.m-input-singlelineedit.position-below {
  display: table;
}
.m-input-singlelineedit.position-below .a-input-singlelineedit {
  margin-left: 0;
}
.m-input-singlelineedit .a-input-singlelineedit {
  margin-bottom: 0;
  margin-right: 0;
}

html[dir=rtl] .m-input-singlelineedit.position-below .a-input-singlelineedit {
  margin-right: 0;
}
html[dir=rtl] .m-input-singlelineedit .a-input-singlelineedit {
  margin-left: 0;
}

/* numerical single-line inputs
 * styles for the input element (that are built on top of the above defaults)
 * along with additional classes to manage the 'spinner' control that appears
 */
.a-input-singlelineedit[type=number] {
  text-align: right;
  width: 197px;
  padding-right: 16px;
}

.show-spinner .a-input-singlelineedit[type=number] {
  padding-right: 28px;
  width: 181px;
}

html[dir=rtl] .show-spinner .a-input-singlelineedit[type=number] {
  padding-right: 0;
  padding-left: 28px;
}

/* remove the default spinner as we are using a custom implementation */
.a-input-singlelineedit[type=number] {
  -moz-appearance: textfield;
}
.a-input-singlelineedit[type=number]::-webkit-inner-spin-button, .a-input-singlelineedit[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  opacity: 0;
  margin: 0;
}

/* remove the clear entry button from IE as it breaks right-aligned text */
.a-input-singlelineedit[type=number]::-ms-clear {
  display: none;
}

/* IE10 does not support :read-only selector */
.read-only .a-input-singlelineedit,
.a-input-singlelineedit.read-only,
.a-input-singlelineedit[readonly],
.a-input-singlelineedit[data-readonly=true] {
  border-color: #847E88 !important;
  cursor: default;
}
.read-only .a-input-singlelineedit.has-content,
.a-input-singlelineedit.read-only.has-content,
.a-input-singlelineedit[readonly].has-content,
.a-input-singlelineedit[data-readonly=true].has-content {
  color: #847E88;
  background-color: #e6e5e7;
}

input.read-only:focus-visible {
  outline: none;
}

/* This following rule shouldn't be necessary, as ready-only elements shouldn't get focus,
 * however I have observed Firefox adding an outline and this prevents that behaviour  */
input:read-only:focus-visible {
  outline: none;
}

input[readonly]:focus-visible {
  outline: none;
}

.m-input-singlelineedit:last-of-type,
.a-input-singlelineedit:last-of-type {
  margin-bottom: 0;
}

.a-input-singlelineedit:not([readonly]):focus {
  outline: none;
  border: 1px solid #E8BE5D;
}

/* inputs when a child of a checkbox (other specifier) */
.m-option-base .a-input-singlelineedit {
  position: relative;
  left: 8px;
  z-index: 1;
  margin: -8px 0 -8px 4px;
  border-radius: 3px 5px 5px 3px;
  border-width: 1px;
  border-color: #4A444E;
  width: 151px;
  height: auto;
}
.m-option-base .a-input-singlelineedit.has-content {
