}
.m-option-base[data-readonly=true] .a-input-singlelineedit,
.m-option-base .read-only .a-input-singlelineedit,
.m-option-base .a-input-singlelineedit.read-only {
  border-color: transparent !important;
}

html[dir=rtl] .m-option-base .a-input-singlelineedit {
  margin-left: 0;
  margin-right: 0.5em;
}

