}

html[dir=rtl] .m-message-error *[class*=-label-],
html[dir=rtl] .m-message-warning *[class*=-label-],
html[dir=rtl] .m-message-success *[class*=-label-] {
  padding: 8px 32px 8px 16px;
}
html[dir=rtl] .m-message-error *[class*=-label-]::before,
html[dir=rtl] .m-message-warning *[class*=-label-]::before,
