.o-structure-table {
  border-collapse: collapse;
  /* the empty-header-row class is added programmatically when the system detects a row
   * that contains headers but is entirely empty - in these cases there should be no border
   */
  /* may remove the following styles if display: none above is the appropriate solution */
}
.o-structure-table tr.m-structure-row {
  border-top: 1px solid #212C4C;
}
.o-structure-table tr.m-structure-row ~ tr.m-structure-row {
  border-top: none;
}
.o-structure-table.shade-headercolumn th[scope=row],
.o-structure-table.shade-headercolumn td[scope=row] {
  background-color: #D6DDEB;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}
.o-structure-table.shade-altcolumns td:nth-child(even),
.o-structure-table.shade-altcolumns th:nth-child(even) {
  background-color: #D6DDEB;
}
.o-structure-table.shade-altcolumns tr:first-child td,
.o-structure-table.shade-altcolumns tr:first-child th {
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
.o-structure-table.shade-altcolumns tr:last-child td {
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}
.o-structure-table.shade-headerrow th[scope=col],
.o-structure-table.shade-headerrow td[scope=col] {
  background-color: #D6DDEB;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
.o-structure-table.shade-altrows tr.striped td,
.o-structure-table.shade-altrows tr.striped th {
  background-color: #D6DDEB;
}
.o-structure-table.shade-altrows tr.striped td:first-child,
.o-structure-table.shade-altrows tr.striped th:first-child {
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}
.o-structure-table.shade-altrows tr.striped td:last-child {
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}
.o-structure-table.shade-altrows tr.striped + tr.striped td:first-child,
.o-structure-table.shade-altrows tr.striped + tr.striped th:first-child {
  border-top-left-radius: 0;
}
.o-structure-table.shade-altrows tr.striped + tr.striped td:last-child {
  border-top-right-radius: 0;
}
.o-structure-table.shade-altrows tr.m-structure-row-error.striped th, .o-structure-table.shade-altrows tr.m-structure-row-error.striped td {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}
.o-structure-table th {
  font-weight: normal;
}
.o-structure-table .grid-row-total-title,
.o-structure-table .grid-column-total-title {
  color: #212C4C;
}
.o-structure-table tr.empty-header-row {
  display: none;
}
.o-structure-table tr.empty-header-row th,
.o-structure-table tr.empty-header-row td {
  border: 0 !important;
}
.o-structure-table .grid-column-total-title,
.o-structure-table .grid-column-total,
.o-structure-table .grid-grandtotal {
  border-top: 1px solid #212C4C;
}
.o-structure-table .grid-row-total-title,
.o-structure-table .grid-row-total,
.o-structure-table .grid-grandtotal {
  border-left: 1px solid #212C4C;
  vertical-align: top;
}
.o-structure-table .grid-row-total,
.o-structure-table .grid-grandtotal {
  text-align: right;
}
.o-structure-table .grid-row-total,
.o-structure-table .grid-column-total,
.o-structure-table .grid-grandtotal {
  white-space: nowrap;
}
.o-structure-table .grid-grandtotal {
  padding-top: 0.5em;
}
.o-structure-table caption {
  caption-side: top;
  text-align: left;
  margin-bottom: 0.5em;
}

html[dir=rtl] .o-structure-table .grid-row-total-title,
html[dir=rtl] .o-structure-table .grid-row-total,
html[dir=rtl] .o-structure-table .grid-grandtotal {
  border-right: 1px solid #212C4C;
}
html[dir=rtl] .o-structure-table .grid-row-total,
html[dir=rtl] .o-structure-table .grid-grandtotal {
  text-align: inherit;
}
html[dir=rtl] .o-structure-table caption {
  text-align: inherit;
}

html:not([dir]) .grid-column-total span {
  padding-right: 0;
}

html[dir=rtl] .grid-column-total span {
  padding-left: 0;
}

/* additional padding to allow for the presence of a number
 * 'spinner' control in input cells above the total */
.show-spinner .grid-column-total span {
  padding-right: 28px;
}

html[dir=rtl] .show-spinner .grid-column-total span {
  padding-right: 0;
  padding-left: 28px;
}

.o-question-container {
  padding: 1em;
  position: relative;
}

.focus-question .o-question-container.focused {
  background-color: white;
}

.o-question-container:not(.sidebyside)[data-position=side] {
  display: inline-block;
}

.o-question-container:not(.no-separator) {
  border-bottom: 1px solid #D6DDEB !important;
}

.surroundcontent > div:last-child {
  border-bottom: none;
}

.sidebyside .o-question-core,
.sidebyside .o-question-messages {
  display: flex;
}

.o-question-information {
  margin-bottom: 0.5em;
}

.sidebyside .o-question-errorandinstruction {
  width: 40%;
  padding-right: 1em;
  box-sizing: border-box;
}

html[dir=rtl] .o-question-errorandinstruction {
  padding-right: 0;
  padding-left: 1em;
}

.sidebyside .o-question-information-and-messages,
.sidebyside .o-question-information-messages {
  box-sizing: border-box;
  margin-right: 1em;
  padding-right: 1em;
  margin-bottom: 0;
  flex: 0 0 40%;
}

html[dir=rtl] .sidebyside .o-question-information {
  margin-right: 0;
  margin-left: 1em;
  padding-right: 0;
  padding-left: 1em;
}

.sidebyside .o-question-core > .o-question-response,
.sidebyside .o-question-core > questions {
  flex: 1;
  flex-basis: auto;
}

.sidebyside .o-question-response[data-position=side] {
  flex-grow: 0;
}

.sidebyside .o-question-response + .o-question-response {
  margin-left: 1em;
}

html[dir=rtl] .sidebyside .o-question-response + .o-question-response {
  margin-left: 0;
  margin-right: 1em;
}

.o-question-scale-horizontal,
.o-question-scale-vertical {
  background-repeat: no-repeat;
  background-size: contain;
}

.o-question-scale-horizontal-control,
.o-question-scale-vertical-control {
  position: relative;
}

.has-container-background .o-scale-unitcontainer {
  border: none;
}

.o-scale-unitcontainer {
  display: flex;
  justify-content: space-around;
  width: 400px;
  max-width: 100%;
  border-radius: 3px;
  border: 1px solid #8797C8;
}

.o-question-scale-vertical .o-scale-unitcontainer {
  flex-direction: column-reverse;
}

.o-label-container {
  display: flex;
}

.o-label-container {
  width: 400px;
  justify-content: space-between;
}

.m-scale-unit {
  position: relative;
  cursor: pointer;
  flex: 1 0 0;
  text-align: center;
  height: 52px;
  align-content: center;
  background-repeat: no-repeat;
  background-position: left center;
}

.o-question-scale-vertical .m-scale-unit {
  flex-basis: auto;
}

.has-unit-background .m-scale-unit {
  flex: 0 0 auto;
}

.m-scale-unit::before {
  position: absolute;
  left: 4px;
  top: 4px;
  display: block;
  content: " ";
  height: calc(100% - 8px);
  width: calc(100% - 8px);
  background-color: #B4BDDB;
  border-radius: 5px;
}

.has-unit-background .m-scale-unit::before {
  content: none;
}

.m-scale-unit.current-value::before {
  background-color: #662582;
}

.m-scale-unit.current-value {
  color: #FAFAFA;
}

.a-image-multistate {
  display: block;
  margin: 0 auto;
  transition: transform 0.3s ease;
}

.a-label-datavalue {
  visibility: hidden;
}

.a-label-datavalue.showValue {
  visibility: visible;
  position: absolute;
  top: 32px;
  left: 30px;
  color: #57699C;
}

.a-label-datavalue.showValue.highlighted {
  color: #FAFAFA;
}

.o-question-media-external .o-media-frame {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  min-height: 200px;
  background: linear-gradient(to right, #8797C8 3px, transparent 3px) 0 0, linear-gradient(to right, #8797C8 3px, transparent 3px) 0 100%, linear-gradient(to left, #8797C8 3px, transparent 3px) 100% 0, linear-gradient(to left, #8797C8 3px, transparent 3px) 100% 100%, linear-gradient(to bottom, #8797C8 3px, transparent 3px) 0 0, linear-gradient(to bottom, #8797C8 3px, transparent 3px) 100% 0, linear-gradient(to top, #8797C8 3px, transparent 3px) 0 100%, linear-gradient(to top, #8797C8 3px, transparent 3px) 100% 100%;
  background-color: #B4BDDB;
  background-repeat: no-repeat;
  background-size: 20px 20px;
}
.o-question-media-external .o-media-frame input[type=button] {
  margin: 0;
}

.a-figure-thumbnail {
  margin: 4px;
}

html {
  height: 95vh;
  width: auto;
}

body {
  background: #FFFFFF;
  color: #212C4C;
  padding: 0;
  margin: 0 2em;
  display: flex;
  flex-direction: column;
  font-size: 20px;
  line-height: 24px;
  font-weight: 400;
}

html[dir=rtl] body,
html[dir=rtl] input,
html[dir=rtl] textarea,
html[dir=rtl] select {
  font-family: "Quicksand", "QuicksandLocal", serif;
}

body,
input,
textarea,
select {
  font-family: "Quicksand", "QuicksandLocal", sans-serif;
}

body > form {
  min-height: 95vh;
  max-height: -webkit-fill-available;
  display: grid;
  grid-template-columns: 1fr minmax(343px, 1660px) 1fr;
  grid-template-rows: 130px auto 110px;
  grid-template-areas: ". header ." ". content ." ". footer .";
}

.header {
  grid-area: header;
  box-sizing: border-box;
  height: 130px;
  padding-bottom: 1em;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  border-bottom: 1px solid #8797C8;
}

.surroundcontent {
  grid-area: content;
  min-height: 66vh;
}

.footer {
  grid-area: footer;
  height: 110px;
  border-top: 1px solid #8797C8;
  padding: 1em 0;
}

.hiddencontrol {
  position: absolute;
  clip: rect(0, 0, 0, 0);
  width: 1px;
  height: 1px;
}

[data-hidden=true] {
  visibility: hidden;
}

.unavailable {
  visibility: hidden;
}

.nowrap {
  white-space: nowrap;
}

.align-left {
  text-align: left !important;
}

.align-center {
  text-align: center !important;
}

.align-right {
  text-align: right !important;
}

@media screen and (max-width: 1023px) {
  body {
    margin: 0;
  }
  .header {
    height: 75px;
    padding: 0 1em 0.8em 1em;
  }
  .footer {
    padding: 1em;
  }
  body > form {
    grid-template-rows: 75px auto 110px;
  }
}
input:disabled {
  opacity: 1;
}

*:focus,
*:focus-visible,
*:focus-within,
*:focus + label,
*:focus-visible + label,
*:focus-within + label {
  border-color: #E8BE5D !important;
}

/* Standardises the placeholder colour - these rules cannot be combined */
::-moz-placeholder {
  color: #4A444E;
  font-size: 14px;
  opacity: 1;
}

:-ms-input-placeholder {
  color: #4A444E;
  font-size: 14px;
  opacity: 1;
}

::placeholder {
  color: #4A444E;
  font-size: 14px;
  opacity: 1;
}

:-ms-input-placeholder {
  color: #4A444E;
  font-size: 14px;
}

::-ms-input-placeholder {
  color: #4A444E;
  font-size: 14px;
}

sup {
  vertical-align: top;
  font-size: 0.6em;
  position: relative;
  top: -4px;
}

sub {
  vertical-align: bottom;
  font-size: 0.6em;
  position: relative;
  top: 4px;
}

.width-20 {
  width: 20%;
}

.width-30 {
  width: 30%;
}

.width-40 {
  width: 40%;
}

.width-50 {
  width: 50%;
}

.width-60 {
  width: 60%;
}

.width-70 {
  width: 70%;
}

.width-80 {
  width: 80%;
}

.width-90 {
  width: 90%;
}

question,
questions {
  display: block;
}

questions[data-position=side] question {
  display: inline-block;
  vertical-align: top;
}
questions[data-position=side] question + question {
  margin-left: 1em;
}